<template>
  <div class="base-snackbar">
    <v-snackbar top v-model="snackbar" :color="response.color">
      {{ response.text }}

      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  name: "BaseSnackbar",
  props: {
    response: {
      type: Object,
    },
  },

  data() {
    return {
      snackbar: true,
    };
  },
};
</script>

