  data() {
    return {
      fullname: "",
      email: "",
      message: "",
      dialog: false,
      response: null,

      // Rules
      fullnameRule: [
        (value) =>
          /^[a-zA-Z]+([\sA-Za-z]+)?$/.test(value) ||
          "Fullname must only contain alphabets",
      ],

      emailRule: [
        (value) =>
          /^[a-zA-Z0-9]+(((.|_|-)[a-zA-Z0-9]+)?)+@(gmail|yahoo|hotmail).(com|fr|uk|net)$/.test(
            value
          ) || "please enter a valid email adresse",
      ],

      messageRule: [
        (value) => value.length > 1 || "Please enter a reasonable message",
      ],

      techstack: [
        { class: "devicon-html5-plain-wordmark colored", title: "html5" },
        { class: "devicon-css3-plain-wordmark colored", title: "css3" },
        { class: "devicon-javascript-plain colored", title: "javascript" },
