  methods: {
    onSendEmail(e) {
      console.log("entered action");
      emailjs
        .sendForm(
          "service_4z2w2oq",
          "template_bucqemv",
          e.target,
          "user_HhlI04fN5UTTBwkriZ6P2"
        )
        .then(
          () => {
            // console.log("SUCCESS!", result.text);
            this.response = {
              text: "Your message has been sent successfully",
              color: "success",
              show: true,
            };
            this.$refs.form.reset();
            this.dialog = false;
          },
          () => {
            // console.log("FAILED...", error.text);
            this.response = {
              text: "Something went wrong!",
              color: "error",
              show: true,
            };
            this.$refs.form.reset();
            this.dialog = false;
          }
        );
    },

    getLg(currentProject) {
      if (currentProject.title === "MyCals") return 6;
      if (currentProject.title === "HireMe") return 12;
      return 3;
    },
  },
