import Vue from "vue";
import Vuex from "vuex";
import createPersistedState from "vuex-persistedstate";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    locale: "en",
    dark: false,
  },
  
  mutations: {
    UPDATE_LOCALE(state, payload) {
      state.locale = payload;
    },

    UPDATE_DARK(state, payload) {
      state.dark = payload;
      // vuex-persistedstate
    },
  },
  actions: {
    UPDATE_LOCALE: (context) => (payload) => {
      context.commit("UPDATE_LOCALE", payload);
    },

    UPDATE_DARK: (context) => (payload) => {
      context.commit("UPDATE_DARK", payload);
    },
  },
  getters: {
    GET_LOCALE: (state) => {
      return state.locale;
    },

    GET_DARK: (state) => {
      return state.dark;
    },
  },
  modules: {},
  plugins: [createPersistedState()],
});
