<template>
  <v-app>
    <v-app-bar
      class="flex-grow-0 elevation-0"
      style="background-color: transparent; border-bottom: 0.5px solid #bdbdbd"
    >
      <v-container class="d-flex align-center justify-space-between">
        <!-- Branding -->
        <router-link :to="{ name: 'Home' }" class="mr-3">
          <span
            class="
              text-h5
              font-weight-black
              text-uppercase
              primary--text
              branding
            "
            >Achraf Fawzi</span
          >
        </router-link>

        <!-- Links -->
        <div class="d-flex align-center">
          <a
            v-for="link in navbarItems"
            :key="link.title"
            :href="link.id"
            class="
              ma-3
              text-uppercase
              subtitle-2
              font-weight-regular
              hidden-sm-and-down
            "
            >{{ link.title }}</a
          >

          <!-- Multilingual menu -->
          <v-menu offset-y>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                depressed
                v-bind="attrs"
                v-on="on"
                class="hidden-sm-and-down"
              >
                <span class="mr-1 text-uppercase">{{
                  $t("navbar.language")
                }}</span>
                <v-icon>keyboard_arrow_down</v-icon>
              </v-btn>
            </template>
            <v-list>
              <v-list-item
                @click="onChangeLanguage(language.value)"
                v-for="(language, index) in languages"
                :key="index"
              >
                <v-list-item-title>{{ language.title }}</v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <!-- <v-switch
            dense
            inset
            hide-details
            :value="darkMode"
            @change="toggleDarkMode"
            :label="`toggle ${switchLabel} mode`"
          ></v-switch> -->
        </div>

        <!-- App Bars -->
        <v-app-bar-nav-icon
          @click="drawer = !drawer"
          class="hidden-md-and-up"
        ></v-app-bar-nav-icon>
      </v-container>
    </v-app-bar>

    <!-- Navigation drawer ====> Responsive navbar -->
    <v-navigation-drawer v-model="drawer" class="elevation-10" app temporary>
      <v-list>
        <v-list-item-group>
          <v-list-item
            v-for="link in navbarItems"
            :key="link.title"
            :href="link.id"
            class="ma-3 text-uppercase subtitle-2 font-weight-regular"
          >
            {{ link.title }}
          </v-list-item>
        </v-list-item-group>
      </v-list>

      <!-- Multilingual menu -->
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-btn depressed v-bind="attrs" v-on="on">
            <span class="mr-1 text-uppercase">Language</span>
            <v-icon>keyboard_arrow_down</v-icon>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            @click="onChangeLanguage(language.value)"
            v-for="(language, index) in languages"
            :key="index"
          >
            <v-list-item-title>{{ language.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-navigation-drawer>

    <!-- Main content -->
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "App",

  computed: {
    navbarItems() {
      return [
        { title: this.$i18n.t("navbar.home"), id: "#home" },
        { title: this.$i18n.t("navbar.about"), id: "#about" },
        { title: this.$i18n.t("navbar.techstack"), id: "#techstack" },
        { title: this.$i18n.t("navbar.projects"), id: "#projects" },
      ];
    },
    switchLabel: function () {
      return this.darkMode ? "light" : "dark";
    },
  },

  data() {
    return {
      // navbarItems: [
      //   { title: "Home", id: "#home" },
      //   { title: "About", id: "#about" },
      //   { title: "Techstack", id: "#techstack" },
      //   { title: "Projects", id: "#projects" },
      //   { title: "Contact", id: "#contact" },
      // ],

      languages: [
        { title: "English", value: "en" },
        { title: "French", value: "fr" },
      ],

      drawer: false,
      darkMode: false,
    };
  },

  mounted() {
    this.$i18n.locale = this.$store.getters.GET_LOCALE;
  },

  methods: {
    onChangeLanguage(locale) {
      this.$store.dispatch("UPDATE_LOCALE", locale);
      this.$i18n.locale = locale;
      this.$router.push({
        params: { lang: locale },
      });
    },
    toggleDarkMode: function () {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      this.darkMode = !this.darkMode;
    },
  },
};
</script>

